import React, { useState, useEffect } from 'react';
import axios from 'axios';

function PostListPage(){
    const [Product, setProducts] = useState([]);

    useEffect(() => {
        axios.get('/api/posts')
            .then(response =>setProducts(response.data))
            .catch(error => console.log(error))
        },[]);

    return (
        <div>
            <h1>Product</h1>
            <table>
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Price</th>
                    <th>Stock</th>
                    <th>Description</th>
                </tr>
                </thead>
                <tbody>
                {Product.map((product) => (
                    <tr key={product.id}>
                        <td>{product.id}</td>
                        <td>{product.name}</td>
                        <td>{product.price}</td>
                        <td>{product.stock}</td>
                        <td>{product.description}</td>
                    </tr>
                ))}
                </tbody>
            </table>
        </div>
    );
}

export default PostListPage;
